<template>
  <div>
    <el-form :model="form" :rules="rules" ref="form" label-width="100px" class="demo-ruleForm">
      <el-form-item prop="testText">
        <!-- 【请输入正确IP格式】 -->
        <el-input v-model="form.testText" style="width: 300px"></el-input>
      </el-form-item>
      <el-form-item label="目的地址">
        <el-radio-group class="radio" v-model="form.targetAddress" @input="handleTargetRadioChange">
          <!-- 所有 -->
          <el-radio label="0">所有</el-radio>
          <!-- IP -->
          <el-radio label="1">IP</el-radio>
          <!-- IP范围 -->
          <el-radio label="2">IP范围</el-radio>
          <!-- IP网段 -->
          <el-radio label="3">IP网段</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="targetAddressText1" v-if="form.targetAddress === '1'">
        <!-- 【请输入正确IP格式】 -->
        <el-input v-model="form.targetAddressText1" style="width: 300px"></el-input>
      </el-form-item>

      <el-form-item class="item-inline" prop="targetAddressText2" v-if="form.targetAddress === '2'">
        <!-- 192.168.0.a-192.168.0.b -->
        <!-- a < b b-a <=99 -->
        <el-input v-model="form.targetAddressText2" style="width: 300px" @input="checkText('small')"></el-input>
      </el-form-item>
      <div class="item-inline">
        <span style="margin: 0 12px" v-if="form.targetAddress === '2'">-</span>
      </div>
      <el-form-item class="item-inline" label-width="0" prop="targetAddressText3" v-if="form.targetAddress === '2'">
        <!-- 192.168.0.a-192.168.0.b -->
        <!-- a < b b-a <=99 -->
        <el-input v-model="form.targetAddressText3" style="width: 300px" @input="checkText('big')"></el-input>
      </el-form-item>

      <el-form-item class="item-inline" prop="targetAddressText4" v-if="form.targetAddress === '3'">
        <el-input v-model="form.targetAddressText4" style="width: 300px"></el-input>
      </el-form-item>
      <div class="item-inline">
        <span style="margin: 0 12px" v-if="form.targetAddress === '3'">/</span>
      </div>
      <el-form-item class="item-inline" :inline-message="true" label-width="0" prop="targetAddressText5" v-if="form.targetAddress === '3'">
        <el-input v-model="form.targetAddressText5" style="width: 80px"></el-input>
      </el-form-item>
    </el-form>

    <div>
      <p>IP: 单个IP地址，例如192.168.0.1</p>
      <p>IP范围: 例如192.168.0.1-192.168.0.150</p>
      <p>IP网段: 例如192.168.0.0/16</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  props: {},
  data() {
    return {
      form: {
        testText: '',
        targetAddress: '',
        targetAddressText1: '',
        targetAddressText2: '',
        targetAddressText3: '',
        targetAddressText4: '',
        targetAddressText5: ''
      },
      rules: {
        testText: [
          {
            validator: this.testValidator,
            trigger: 'change'
          }
        ],
        targetAddressText1: [
          {
            validator: this.validateIp46
          }
        ],
        targetAddressText2: [
          {
            validator: this.validateIp46
          }
        ],
        targetAddressText3: [
          {
            validator: this.validateIp46
          }
        ],
        targetAddressText4: [
          {
            validator: this.validateIp46
          }
        ],
        targetAddressText5: [
          {
            validator: this.validateMask
          }
        ]
      }
    }
  },
  methods: {
    handleTargetRadioChange(val) {
      // 清空输入框
      this.form.targetAddressText1 = ''
      this.form.targetAddressText2 = ''
      this.form.targetAddressText3 = ''
      this.form.targetAddressText4 = ''
      this.form.targetAddressText5 = ''
      this.$refs.form.clearValidate()
    },
    checkText(type) {
      if (type === 'small') {
        this.$refs.form.validateField('targetAddressText3')
      } else if (type === 'big') {
        this.$refs.form.validateField('targetAddressText2')
      }
    },
    testValidator(rule, value, callback) {
      const ipReg = /^(?:(?:[1-9]\d?|1\d{2}|2[0-4]\d|25[0-5]|0)(?:\.|$)){4}$|^(?:(?:(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4}))*::([0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*))|(?:(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4}))*::(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*))|(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})){7}[0-9a-fA-F]{1,4})(?<!\.)$/
      const anyReg = /^any$/
      const numReg = /^(3[01]|[12]\d|[1-9])$/
      if (!value || value == null) {
        callback()
        return
      }

      if (anyReg.test(value)) {
        callback()
        return
      }

      if (value.includes('-')) {
        const ipArr = value.split('-')
        if (!ipReg.test(ipArr[0]) || !ipReg.test(ipArr[1])) {
          callback(new Error('请输入正确IP格式'))
          return
        }
        const twoArr = ipArr[0].split('.')
        const twoFirst = parseInt(twoArr[0])
        const twoSecond = parseInt(twoArr[1])
        const twoThird = parseInt(twoArr[2])
        const twoFourth = parseInt(twoArr[3])
        const threeArr = ipArr[1].split('.')
        const threeFirst = parseInt(threeArr[0])
        const threeSecond = parseInt(threeArr[1])
        const threeThird = parseInt(threeArr[2])
        const threeFourth = parseInt(threeArr[3])
        if (twoFourth >= threeFourth) {
          // 您输入的IP地址范围有误，起始IP地址应该小于结束IP地址
          callback(new Error('您输入的IP地址范围有误，起始IP地址应该小于结束IP地址'))
          return
        }
        if (threeFourth - twoFourth > 99) {
          // IP范围最大不超过100
          callback(new Error('IP范围最大不超过100'))
          return
        }
        if ((twoFirst !== threeFirst) || (twoSecond !== threeSecond) || (twoThird !== threeThird)) {
          // 请检查并重新输入两个属于同一网络的IP地址。例如:IP地址1:192.168.1.20，IP地址2:192.168.1.30
          callback(new Error('请检查并重新输入两个属于同一网络的IP地址。例如:IP地址1:192.168.1.20，IP地址2:192.168.1.30'))
          return
        }
        callback()
        return
      }

      if (value.includes('/')) {
        const ipArr = value.split('/')
        if (!ipReg.test(ipArr[0])) {
          callback(new Error('请输入正确IP格式'))
          return
        }
        if (ipArr[1] && !numReg.test(ipArr[1])) {
          callback(new Error('抱歉，您输入的数字超出该范围，只能填写1到31之间的数字'))
          return
        }
        callback()
        return
      }

      if (!ipReg.test(value)) {
        callback(new Error('请输入正确IP格式'))
        return
      }
      callback()
    },
    validateIp46(rule, value, callback) {
      if (!value) {
        callback()
        return
      }
      const pattern = /^(?:(?:25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(?:\.|$)){4}$|^(?:(?:(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*)?::([0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*)?)|(?:(?:[0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*)?::([0-9a-fA-F]{1,4}(?::[0-9a-fA-F]{1,4})*)?))(?<![0])(?<!\.)$/
      if (value && !pattern.test(value)) {
        callback(new Error('请输入正确IP格式'))
        return
      }
      if (rule.field === 'targetAddressText2' || rule.field === 'targetAddressText3') {
        if (this.form.targetAddressText2 && this.form.targetAddressText3) {
          const twoArr = this.form.targetAddressText2.split('.')
          const twoFirst = parseInt(twoArr[0])
          const twoSecond = parseInt(twoArr[1])
          const twoThird = parseInt(twoArr[2])
          const twoFourth = parseInt(twoArr[3])
          const threeArr = this.form.targetAddressText3.split('.')
          const threeFirst = parseInt(threeArr[0])
          const threeSecond = parseInt(threeArr[1])
          const threeThird = parseInt(threeArr[2])
          const threeFourth = parseInt(threeArr[3])
          if (twoFourth >= threeFourth) {
            callback(new Error('您输入的IP地址范围有误，起始IP地址应该小于结束IP地址'))
            return
          }
          if (threeFourth - twoFourth > 99) {
            callback(new Error('IP范围最大不超过100'))
            return
          }
          if ((twoFirst !== threeFirst) || (twoSecond !== threeSecond) || (twoThird !== threeThird)) {
            callback(new Error('请检查并重新输入两个属于同一网络的IP地址。例如:IP地址1:192.168.1.20，IP地址2:192.168.1.30'))
            return
          }
        }
      }
      callback()
    },
    validateMask(rule, value, callback) {
      if (!value) {
        callback()
        return
      }

      value = parseInt(value)

      if (value < 1 || value > 31) {
        callback(new Error('抱歉，您输入的数字超出该范围，只能填写1到31之间的数字'))
      }

      callback()
    }
  }
}
</script>

<style scoped lang='scss'>
.item-inline {
  display: inline-block;
}
</style>
